// eslint-disable-next-line @typescript-eslint/no-explicit-any
export type Listener = (payload: any) => void
export interface EventManager {
  add(eventName: string, listener: Listener): void
  remove(eventName: string, listener: Listener): void
}

export class EventWrapper {
  listeners: Map<string, Listener> = new Map()
  listenerManager: EventManager

  constructor(listenerManager: EventManager) {
    this.listenerManager = listenerManager
  }

  addListener(eventName: string, listener: Listener): void {
    this.listeners.set(eventName, listener)
    this.listenerManager.add(eventName, listener)
  }

  removeListener(eventName: string): void {
    const listener = this.listeners.get(eventName)
    if (!listener) {
      return
    }

    this.listenerManager.remove(eventName, listener)
    this.listeners.delete(eventName)
  }

  on(eventName: string, listener: Listener): void {
    // Remove previous listener
    this.removeListener(eventName)

    // Add new listener
    this.addListener(eventName, listener)
  }

  once(eventName: string, listener: Listener): void {
    // eslint-disable-next-line @typescript-eslint/no-explicit-any
    const onceWrapper = (payload: any) => {
      listener(payload)
      this.removeListener(eventName)
    }
    this.addListener(eventName, onceWrapper)
  }

  off(eventName: string): void {
    if (!this.listeners.has(eventName)) {
      return
    }

    this.removeListener(eventName)
  }

  clear(): void {
    this.listeners.forEach((value, key) => {
      this.listenerManager.remove(key, value)
    })
    this.listeners.clear()
  }
}
