import { AdsConfig } from 'defines/alldefines';
import { EventWrapper } from './event-wrapper'

declare class RApp {
  static postMessage(msg: any): void
}

function emit(
  eventName: string,
  source: string,
  payload: any | null = null
): void {
  const message = { type: eventName, source, data: payload || {} }
  //eslint-disable-next-line no-undef
  if (typeof RApp !== 'undefined') {
    RApp.postMessage(JSON.stringify(message));
  } else {
    // eslint-disable-next-line no-console
    console.log(`emit: ${JSON.stringify(message)}`)
  }
}

export class Platform {
  private source: string
  private eventWrapper: EventWrapper = new EventWrapper({
    add(eventName: string, listener: (payload: any) => void): void {
      window.document.addEventListener(eventName, listener)
    },
    remove(eventName: string, listener: (payload: any) => void): void {
      window.document.removeEventListener(eventName, listener)
    },
  })

  constructor(source: string) {
    this.source = source
  }

  showAds(config: AdsConfig): void {
    emit(`show_${config.adsType}_ads`, this.source, { ...config })
  }

  startGame(): void {
    emit('request_game_data', this.source)
  }

  emitCustomEvent(eventName: string, payload: any) {
    emit(eventName, this.source, payload)
  }

  on(eventName: string, listener: (payload: any) => void): void {
    this.eventWrapper.on(eventName, this.transform(listener))
  }

  off(eventName: string): void {
    this.eventWrapper.off(eventName)
  }

  once(eventName: string, listener: (payload: any) => void): void {
    this.eventWrapper.once(eventName, this.transform(listener))
  }

  clear(): void {
    this.eventWrapper.clear()
  }

  transform(listener: (payload: any) => void): (payload: any) => void {
    const transformedListener = (event: any) => {

      if (event.detail) {
        if (typeof event.detail === 'string')
          listener(JSON.parse(event.detail))
        else
          listener(event.detail)
      } else {
        listener({
          detail: {},
          message: "Detail NULL"
        })
      }
    }
    return transformedListener
  }
}
