import { Platform } from './platform'
import Config from './config.json'
import ScriptDownloader from 'component/ScriptDownloader'
import { AdsConfig } from 'defines/alldefines'
import NetworMgr from 'component/NetworkMgr'

const platform = new Platform(`ROSNInstant-${Config.version}`)

declare namespace window {
    function vConsole(): void
}
export function getSDKVersion(): string {
    return Config.version
}

export function initializeAsync(): Promise<any> {
    return new Promise(resolve => {
        // platform.once('game_data_response', payload => {
        //   resolve(payload)
        // })
        // platform.startGame()
        console.log(`ROSNInstant version ${getSDKVersion()} initialize success`)
        resolve({})
    })
}

/**
 * @param {AdsConfig} config reward, kind of ads, num expectation
 * @param {Function} onSuccess trigger on view ads success
 * @param {Function} onFailure trigger on view ads failed
 **/
export function showAds(
    config: AdsConfig = { adsType: "interstitial", num: 0, rewardType: "SCORE" },
    onSuccess: (reward: Object) => void = () => { },
    onFailure: () => void = () => { }
) {

    //validate params
    const adsType = ['reward', 'interstitial'].find(ads => ads === config.adsType) || 'interstitial';
    config.num = config.num || null;

    const offEvents = () => {
        platform.off(`show_${adsType}_ad_success`)
        platform.off(`show_${adsType}_ad_fail`)
    }

    const successCallback = (reward: Object) => {
        offEvents()
        onSuccess(reward)
    }

    const failureCallback = () => {
        offEvents()
        onFailure()
    }

    platform.on(`show_${adsType}_ad_success`, successCallback)
    platform.on(`show_${adsType}_ad_fail`, failureCallback)
    platform.showAds(config);
}

export function downloadScript(scriptUrl: string): Promise<any> {
    return ScriptDownloader.downloadScript(scriptUrl);
}

export function injectDebug() {
    ScriptDownloader.downloadScript(
        'https://unpkg.com/vconsole@3.14.6/dist/vconsole.min.js'
    ).then(() => {
        //eslint-disable-next-line
        new (window.vConsole as any)();
    })
}

export function makeRequest(url: string, method: string, params: Object, token: string): Promise<Object> {
    return NetworMgr.makeRequestUrl(url, method, params, token);
}
