export default class ScriptDownloader {
  private static downloaded: any = {}

  //eslint-disable-next-line no-undef
  private static parseParameters(
    options: Function | any,
    onProgress: Function | any,
    onComplete: Function | any
  ) {
    if (onComplete === undefined) {
      var isCallback = typeof options === 'function'
      if (onProgress) {
        onComplete = onProgress
        if (!isCallback) {
          onProgress = null
        }
      } else if (onProgress === null && isCallback) {
        onComplete = options
        options = null
        onProgress = null
      }
      if (onProgress !== null && isCallback) {
        onProgress = options
        options = null
      }
    }
    options = options || Object.create(null)
    return { options, onProgress, onComplete }
  }

  public static downloadScript(url: string) {
    return ScriptDownloader.downloadScriptWeb(url)
  }
  private static downloadScriptWeb(url: string): Promise<void> {
    let promise = new Promise<void>((resolve, reject) => {
      var { options, onComplete } = ScriptDownloader.parseParameters(
        options,
        null,
        onComplete
      )

      // no need to load script again
      if (ScriptDownloader.downloaded[url]) {
        resolve()
        return
      }

      let d = document
      let s = document.createElement('script')

      // if (window.location.protocol !== 'file:') {
      //   s.crossOrigin = 'anonymous'
      // }

      s.async = options.async
      s.src = url
      let loadHandler = () => {
        s.parentNode?.removeChild(s)
        s.removeEventListener('load', loadHandler, false)
        s.removeEventListener('error', errorHandler, false)
        ScriptDownloader.downloaded[url] = true
        resolve()
      }

      let errorHandler = () => {
        s.parentNode?.removeChild(s)
        s.removeEventListener('load', loadHandler, false)
        s.removeEventListener('error', errorHandler, false)
        console.log(
          'ScriptDownloader:downloadScriptWeb - ' + 'Cannot load ' + url
        )
        reject()
      }

      s.addEventListener('load', loadHandler, false)
      s.addEventListener('error', errorHandler, false)
      d.body.appendChild(s)
    })

    return promise
  }
}
